p{
    margin: 0;
}

.listOfContact{
    list-style: square;
}

.itemOfListContact{
    display: flex;
    align-items: center;
    margin-bottom: 15px;
}

.itemNameContact{
    margin-right: 10px;
    font-style: italic;
}

.itemNumberContact{
    margin-right: 10px;
}

.btnOfListContact {
    height: 20px;
    min-width: 30px;
}

.btnOfListContact{
    background-color: rgb(191, 200, 248);
    border-radius: 50%;
    cursor: pointer;
    box-shadow: 0px 0 4px rgba(0,0,0,0.5);
}
